* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
  }
  
  body {
    font-family: "Roboto", sans-serif;
  }
  
  #app {
    display: flex;
    height: 100vh;
  }
  
  .col {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
  }
  
  .col h2 {
    padding: 10px;
    color: white;
    border-radius: 10px;
    transition: background 0.3;
  }
  
  .col h2:hover {
    background: rgba(0, 0, 0, 0.1);
    cursor: pointer;
  }
  
  .col button {
    outline: none;
    border: none;
    background: transparent;
    font-size: 1.5rem;
    color: white;
    transition: background 0.3;
    padding: 10px;
    border-radius: 10px;
  }
  
  .col button:hover {
    background: rgba(0, 0, 0, 0.1);
    cursor: pointer;
  }
  